import { createStore } from 'vuex';
import UserSession from '../models/UserSession'; // Importa la clase UserSession

export default createStore({
  state() {
    return {
      variable: 'Servicio',
      cart: [],
      user: new UserSession(
        localStorage.getItem('username') || null,
        localStorage.getItem('token') || null
      ) // Inicializa la sesión del usuario desde localStorage
    };
  },
  getters: {
    cartItems(state) {
      return state.cart;
    },
    isAuthenticated(state) {
      return state.user.isValid(); // Usa el método isValid para verificar la autenticación
    },
    cartTotalQuantity(state) {
      return state.cart.reduce((total, item) => total + item.quantity, 0);
    },
    cartTotalPrice(state) {
      return state.cart.reduce((total, item) => total + item.quantity * item.price, 0);
    },
    user(state) {
      return state.user;
    }
  },
  mutations: {
    SET_USER(state, userData) {
      const user = new UserSession(userData.username, userData.token);
      state.user = user;
      localStorage.setItem('username', userData.username); // Guarda el nombre de usuario en localStorage
      localStorage.setItem('token', userData.token); // Guarda el token en localStorage
    },
    LOGOUT(state) {
      state.user = new UserSession(null, null); // Resetea la sesión
      localStorage.removeItem('username');
      localStorage.removeItem('token');
    },
    setVariable(state, newValue) {
      state.variable = newValue;
    },
    addToCart(state, product) {
      const item = state.cart.find(item => item.id === product.id);
      if (item) {
        item.quantity += 1;
      } else {
        state.cart.push({ ...product, quantity: 1 });
      }
    },
    removeProductFromCart(state, productId) {
      const index = state.cart.findIndex(item => item.id === productId);
      if (index !== -1) {
        state.cart.splice(index, 1);
      }
    },
    clearCart(state) {
      state.cart = [];
    },
    updateCartQuantity(state, { id, quantity }) {
      const item = state.cart.find(item => item.id === id);
      if (item) {
        item.quantity = quantity;
      }
    },
    restoreCartState(state, previousState) {
      state.cart = previousState;
    }
  },
  actions: {
    updateVariable({ commit }, newValue) {
      commit('setVariable', newValue);
    },
    addToCart({ commit }, product) {
      return new Promise((resolve) => {
        commit('addToCart', product);
        resolve();
      });
    },
    removeProductFromCart({ commit }, productId) {
      commit('removeProductFromCart', productId);
    },
    clearCart({ commit }) {
      commit('clearCart');
    },
    updateCartQuantity({ commit }, payload) {
      commit('updateCartQuantity', payload);
    },
    restoreCartState({ commit }, previousState) {
      commit('restoreCartState', previousState);
    },
    login({ commit }, { username, password }) {
      return new Promise((resolve, reject) => {
        // Simulación de autenticación
        if (username === 'admin' && password === 'admin') {
          const user = { username: 'Missael', token: 'abc123' }; // Ejemplo de token
          commit('SET_USER', user);
          resolve();
        } else {
          reject(new Error('Credenciales inválidas'));
        }
      });
    },
    logout({ commit }) {
      commit('LOGOUT');
    }
  }
});
