<template>
    <div class="login-page flex justify-center items-center h-screen bg-gray-100">
      <div class="login-form bg-white p-8 rounded-lg shadow-lg w-full max-w-md">
        <h1 class="text-2xl font-bold mb-6 text-center">Iniciar Sesión</h1>
        <form @submit.prevent="handleLogin">
          <div class="mb-4">
            <label for="username" class="block text-gray-700 text-sm font-bold mb-2">Usuario</label>
            <input v-model="username" type="text" id="username" required
              class="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500" />
          </div>
          <div class="mb-6">
            <label for="password" class="block text-gray-700 text-sm font-bold mb-2">Contraseña</label>
            <input v-model="password" type="password" id="password" required
              class="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500" />
          </div>
          <div class="flex items-center justify-between">
            <button type="submit"
              class="bg-blue-600 text-white font-semibold px-4 py-2 rounded-lg hover:bg-blue-700 focus:ring-2 focus:ring-blue-500">
              Iniciar Sesión
            </button>
          </div>
        </form>
        <p v-if="errorMessage" class="text-red-500 mt-4 text-center">{{ errorMessage }}</p>
      </div>
    </div>
  </template>
  
  <script>
  import { mapActions, mapState } from 'vuex';
  
  export default {
    name: 'LoginPage',
    data() {
      return {
        username: 'admin',
        password: 'admin',
        errorMessage: null,
      };
    },
    computed: {
      ...mapState(['user'])
    },
    methods: {
      ...mapActions(['login']),
      handleLogin() {
        const userData = {
          username: this.username,
          password: this.password
        };
  
        this.login(userData)
          .then(() => {
            this.$router.push('/'); // Redirige al inicio después de login
          })
          .catch(error => {
            this.errorMessage = 'Usuario o contraseña incorrectos.' + error;
          });
      }
    }
  };
  </script>
  
  <style scoped>
  .login-page {
    background-size: cover;
    background-position: center;
  }
  </style>
  